<template>
<div>
  <el-card class="box-card">
    <!--  -->
    <div slot="header" class="clearfix">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <!--  -->
    <!--  -->
    <el-form ref="form" label-width="80px">
      <el-form-item label="">
        <el-radio-group v-model="status">
          <el-radio :label="null"></el-radio>
          <el-radio :label="0"></el-radio>
          <el-radio :label="1"></el-radio>
          <el-radio :label="2"></el-radio>
          <el-radio :label="3"></el-radio>
          <el-radio :label="4"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="channelId" placeholder="">
          <el-option label="" :value="null"></el-option>
          <el-option v-for="item in channels" :key="item.id" :label="item.name" :value="item.id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <div class="block">
          <el-date-picker
            v-model="value"
            type="daterange"
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd">
          </el-date-picker>
        </div>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="filter"></el-button>
      </el-form-item>
    </el-form>
<!--  -->
  </el-card>
<!--  -->
  <el-card class="box-card2">
    <div slot="header" class="clearfix">
       {{total}} 
    </div>
    <template>
    <el-table
      v-loading="loading"
      :data="articles"
      style="width: 100%">
      <el-table-column
        label=""
        width="230">
        <template slot-scope="scope">
          <el-image
            style="width: 100px; height: 100px"
            :src="scope.row.cover.images[0]"
            fit="cover"
            lazy>
            <div slot="placeholder" style="text-align: center">
              <span class="dot">...</span>
            </div>
          </el-image>
        </template>
      </el-table-column>
      <el-table-column
        prop="title"
        label=""
        width="230">
      </el-table-column>
      <el-table-column
        label=""
        width="230">
        <template slot-scope="scope">
          <el-tag :type="state[scope.row.status].type">{{state[scope.row.status].text}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column
        prop="pubdate"
        label=""
        width="230">
      </el-table-column>
      <el-table-column
        prop="address"
        label="">
        <template slot-scope="scope">
          <el-button type="primary" icon="el-icon-edit" circle @click="$router.push(`/publish?id=${scope.row.id}`)"></el-button>
          <el-button type="danger" icon="el-icon-delete" circle @click="delArticles(scope.row.id)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </template>
  <el-pagination
    background
    layout="prev, pager, next"
    :total="total"
    :disabled="loading"
    :page-size="pageSize"
    :current-page.sync="page"
    @current-change="gainPage">
  </el-pagination>
  </el-card>
<!--  -->
</div>
</template>

<script>
import { articleSearch, articlechannels, delArticle } from '@/api/article'
export default {
  name: 'articleMent',
  props: {},
  components: {},
  data () {
    return {
      value: null,
      articles: [],
      state: [
        { status: 0, text: '', type: 'info' },
        { status: 1, text: '' },
        { status: 2, text: '', type: 'success' },
        { status: 3, text: '', type: 'warning' },
        { status: 4, text: '', type: 'danger' }
      ],
      page: 1,
      pageSize: 10,
      total: null,
      loading: false,
      status: null,
      channels: [],
      channelId: null
    }
  },
  computed: {},
  watch: {},
  // 
  methods: {
    // 
    getArticle (page) {
      this.loading = true
      articleSearch({
        // 
        page,
        // 
        per_page: this.pageSize,
        status: this.status,
        channel_id: this.channelId,
        // this.valuenullthis.value[0]
        begin_pubdate: this.value ? this.value[0] : null,
        end_pubdate: this.value ? this.value[1] : null
      }).then(res => {
        // console.log(res)
        this.loading = false
        this.articles = res.data.data.results
        // 
        this.total = res.data.data.total_count
        // console.log(this.articles)
      })
    },
    // 
    gainPage () {
      // console.log(this.page)
      this.getArticle(this.page)
    },
    // 
    filter () {
      this.getArticle(1)
    },
    // 
    getChannels () {
      articlechannels().then(res => {
        // console.log(res)
        // console.log(res.data.data.channels[0].name)
        this.channels = res.data.data.channels
      })
    },
    // 
    delArticles (id) {
      console.log(id)
      console.log(id.toString())
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        delArticle(id.toString()).then(res => {
          this.$message({
            message: '',
            type: 'success'
          })
          this.getArticle(this.page)
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    }
  },
  created () {
    this.getArticle(1)
    this.getChannels()
  },
  mounted () {}
}
</script>
<style lang='less' scoped>
.box-card {
  margin-bottom: 20px;
}
.box-card2 {
  margin-bottom: 20px;
}
</style>
